'use client';

import { useState } from 'react';
import { Namespace } from '../../types/Namespace';

interface NamespaceModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (data: Omit<Namespace, 'namespace-id'>) => Promise<void>;
  initialData?: Namespace;
}

export default function NamespaceModal({ isOpen, onClose, onSave, initialData }: NamespaceModalProps) {
  const [formData, setFormData] = useState({
    'namespace-name': initialData?.['namespace-name'] || '',
    'namespace-url': initialData?.['namespace-url'] || ''
  });

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-[500px]">
        <h2 className="text-lg font-medium mb-4">
          {initialData ? 'Edit Namespace' : 'Create Namespace'}
        </h2>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">Name</label>
            <input
              type="text"
              value={formData['namespace-name']}
              onChange={(e) => setFormData(prev => ({
                ...prev,
                'namespace-name': e.target.value
              }))}
              className="w-full px-3 py-2 border rounded"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-1">Base URL</label>
            <input
              type="text"
              value={formData['namespace-url']}
              onChange={(e) => setFormData(prev => ({
                ...prev,
                'namespace-url': e.target.value
              }))}
              className="w-full px-3 py-2 border rounded"
            />
          </div>

          <div className="flex justify-end gap-2 mt-6">
            <button
              onClick={onClose}
              className="px-4 py-2 text-gray-600 hover:text-gray-800"
            >
              Cancel
            </button>
            <button
              onClick={() => onSave(formData)}
              className="px-4 py-2 bg-[#ff6b4a] text-white rounded hover:bg-[#ff5436]"
            >
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
  );
} 