'use client';

import { useState } from 'react';
import { NamespaceAccount } from '../../types/Namespace';
import { FiPlus, FiTrash2 } from 'react-icons/fi';

interface AccountModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (data: Omit<NamespaceAccount, 'namespace-account-id'>) => Promise<void>;
  namespaceId: string;
  namespaceName: string;
  initialData?: NamespaceAccount;
}

export default function AccountModal({ 
  isOpen, 
  onClose, 
  onSave, 
  namespaceId, 
  namespaceName,
  initialData 
}: AccountModalProps) {
  const [formData, setFormData] = useState({
    'namespace-id': namespaceId,
    'namespace-name': namespaceName,
    'namespace-account-url-override': initialData?.['namespace-account-url-override'] || '',
    'namespace-account-header': initialData?.['namespace-account-header'] || [{ key: '', value: '' }]
  });

  const addHeader = () => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-header': [...prev['namespace-account-header'], { key: '', value: '' }]
    }));
  };

  const removeHeader = (index: number) => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-header': prev['namespace-account-header'].filter((_, i) => i !== index)
    }));
  };

  const updateHeader = (index: number, field: 'key' | 'value', value: string) => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-header': prev['namespace-account-header'].map((header, i) => 
        i === index ? { ...header, [field]: value } : header
      )
    }));
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-[600px]">
        <h2 className="text-lg font-medium mb-4">
          {initialData ? 'Edit Account' : 'Create Account'}
        </h2>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">URL Override</label>
            <input
              type="text"
              value={formData['namespace-account-url-override']}
              onChange={(e) => setFormData(prev => ({
                ...prev,
                'namespace-account-url-override': e.target.value
              }))}
              className="w-full px-3 py-2 border rounded"
            />
          </div>

          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="block text-sm font-medium">Headers</label>
              <button
                onClick={addHeader}
                className="text-[#ff6b4a] hover:text-[#ff5436] text-sm flex items-center gap-1"
              >
                <FiPlus /> Add Header
              </button>
            </div>
            
            <div className="space-y-2">
              {formData['namespace-account-header'].map((header, index) => (
                <div key={index} className="flex gap-2">
                  <input
                    type="text"
                    value={header.key}
                    onChange={(e) => updateHeader(index, 'key', e.target.value)}
                    placeholder="Key"
                    className="flex-1 px-3 py-2 border rounded"
                  />
                  <input
                    type="text"
                    value={header.value}
                    onChange={(e) => updateHeader(index, 'value', e.target.value)}
                    placeholder="Value"
                    className="flex-1 px-3 py-2 border rounded"
                  />
                  <button
                    onClick={() => removeHeader(index)}
                    className="text-red-500 hover:text-red-600"
                  >
                    <FiTrash2 />
                  </button>
                </div>
              ))}
            </div>
          </div>

          <div className="flex justify-end gap-2 mt-6">
            <button
              onClick={onClose}
              className="px-4 py-2 text-gray-600 hover:text-gray-800"
            >
              Cancel
            </button>
            <button
              onClick={() => onSave(formData)}
              className="px-4 py-2 bg-[#ff6b4a] text-white rounded hover:bg-[#ff5436]"
            >
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
  );
} 